.banner1{
    display: flex;



    img
    {
       padding: 1rem  0px;
       flex: 2;
       object-fit: cover;
       max-width: 100%;
       width: 550px;
       height: 400px;      
        
    }
}
.breath{
  height: 20%;
}
.banner1-left
{
    @extend .display-xl-48-dekstop-only ;
    position: relative;
    text-align: center;
    color: white;
    width: 554px;
    height: 432px;
    max-width: 100%;
    object-fit: cover;
    padding: 0.5em;
    flex: 1;
    background-color:#1B252C;

    h1{
        @extend .display-xl-48-dekstop-only ;
    }
    p{
        @extend .display-s-20-;
    }
 
  .shop-D {
    @extend .regular-heading-s-14-;
    color: white;
    background-color:#1B252C;
    border: 2px solid #FFFFFF;
    width: 14rem;
    padding: 1em;
    border-radius: 0.75em;
    opacity: 100%;
  }
  
}
.banner1-bottom
{

    @extend .display-xl-48-dekstop-only ;
    position: absolute;
    margin: auto;
   
    background-color:  $primary-brand;
  
    right: 74%;
    transform: translateX(-50%);
    bottom: 25%;
    



}

.mapiconWrapper {
  position: absolute;
  left: 0%;
  bottom: 10%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
 margin-left: -1em;

  .mapPinImg {
      width: 14%;
      margin-bottom: 0.75em;

  }

  .mapMaxHeaderImg {
      width: 28%;
      margin-left: -1em;

  }

}